export default {
  data() {
    return {
      received_photo: null,
      received_message: null,
    };
  },
  mounted() {
    window.addEventListener('message', (e) => {
      if (this.isJsonString(e.data)) {
        const data = JSON.parse(e.data);
        const value = data.value;
        switch (data.name) {
          case 'RESPONSE_PHOTO':
            this.onReceivePhoto(value);
            break;

          case 'MESSAGE_FROM_FLUTTER':
            this.messageFromFlutter(value);
            break;
        }
      }
    });
  },
  methods: {
    requestPhoto() {
      this.sendMessageToFlutter('REQUEST_PHOTO');
    },
    hideBottomNavigate() {
      this.sendMessageToFlutter('HIDE_BOTTOM_NAVIGATE');
    },
    showBottomNavigate() {
      this.sendMessageToFlutter('SHOW_BOTTOM_NAVIGATE');
    },
    onReceivePhoto(base64) {
      this.received_photo = 'data:image/jpeg;base64,' + base64;
    },
    sendMessageToFlutter(message) {
      try {
        parent.postMessage(message, '*');
        window.messageHandler.postMessage(message);
      } catch (error) {
        console.log('ðŸ˜€');
      }
    },
    messageFromFlutter(message) {
      this.received_message = message;
    },
    isJsonString(str) {
      try {
        JSON.parse(str);
      } catch (e) {
        return false;
      }
      return true;
    },
  },
};
